<template>
    <SilentForm @update-opt="updateOpt"></SilentForm>
    <AuthProxyForm :isSilent="isSilent" @update-opt="updateOpt"></AuthProxyForm>
</template>

<script>
import { onMounted, reactive, ref, watch } from 'vue';
import SilentForm from '@/components/forms/SilentForm.vue'
import AuthProxyForm from '@/components/forms/AuthProxyForm.vue'


export default {
    name: 'SetupCmd',
    components: {
        SilentForm,
        AuthProxyForm,
    },
    setup(_, { emit }) {
        // #############
        // フォーム更新 #
        // #############
        const optDict = reactive({
            silent: "--silent",
            authProxy: "",
        })

        // 共通化
        const updateOpt = async (key, opt) => {
            optDict[key] = opt
            await updateOpts()
        }

        // #############
        // コマンド生成 #
        // #############
        const updateOpts = async () => {
            let createdOpt = ""
            for (const key in optDict) {
                if (optDict[key]) {
                    createdOpt += ` ${optDict[key]}`
                }
            }

            // コマンド生成&表示は親コンポーネント
            emit("update-cmd", createdOpt)
        }

        // ####################
        // サイレントモード監視 #
        // * reactiveはcomputedで監視できない
        // ####################
        const isSilent = ref(true)
        watch(optDict, () => {
            isSilent.value = optDict?.silent !== ""
        })

        // ###############
        // ライフサイクル #
        // ###############
        onMounted(async () => {
            await updateOpts()
        })

        return {
            // オプション
            optDict,
            // コマンド生成
            updateOpt,
            // サイレント
            isSilent,
        }
    }
}
</script>
