<template>
    <SilentForm @update-opt="updateOpt"></SilentForm>
    <SyncForm :showUseExistingTestOpt=true @update-opt="updateOpt"></SyncForm>
    <TestrunNameForm @update-opt="updateOpt" @update-qt-testrun="updateQtTestrun"></TestrunNameForm>
    <BrowserForm @update-opt="updateOpt"></BrowserForm>
    <TestrunMemoForm @update-opt="updateOpt"></TestrunMemoForm>
    <ReportDirForm @update-opt="updateOpt"></ReportDirForm>
    <TestrunProxyForm :isSilent="isSilent" @update-opt="updateOpt"></TestrunProxyForm>

    <!-- 外部連携 -->
    <QtForm :optDict="optDict" :testrunNameList="testrunNameList" :isSilent="isSilent" @update-opt="updateOpt">
    </QtForm>
    <AnyTestForm :optDict="optDict" :isSilent="isSilent" @update-opt="updateOpt"></AnyTestForm>
</template>

<script>
import { onMounted, reactive, ref, watch } from 'vue'

import TestrunNameForm from '@/components/forms/TestrunNameForm.vue'
import SilentForm from '@/components/forms/SilentForm.vue'
import SyncForm from '@/components/forms/SyncForm.vue'
import BrowserForm from '@/components/forms/BrowserForm.vue'
import TestrunMemoForm from '@/components/forms/TestrunMemoForm.vue'
import ReportDirForm from '@/components/forms/ReportDirForm.vue'
import TestrunProxyForm from '@/components/forms/TestrunProxyForm.vue'
import QtForm from '@/components/forms/QtForm.vue'
import AnyTestForm from '@/components/forms/AnyTestForm.vue'

export default {
    name: 'TestrunCmd',
    components: {
        TestrunNameForm,
        SilentForm,
        SyncForm,
        BrowserForm,
        TestrunMemoForm,
        ReportDirForm,
        TestrunProxyForm,
        QtForm,
        AnyTestForm,
    },
    setup(_, { emit }) {
        // #############
        // フォーム更新 #
        // #############
        const optDict = reactive({
            testrunName: "",
            silent: "--silent",
            sync: "",
            browser: "",
            testrunMemo: "",
            reportsDir: "",
            testProxy: "",
            qt: "",
            anytest: "",
        })

        // 共通化
        const updateOpt = async (key, opt) => {
            optDict[key] = opt
            await updateOpts()
        }

        // #############
        // コマンド生成 #
        // #############
        const updateOpts = async () => {
            let createdOpt = ""
            for (const key in optDict) {
                if (optDict[key]) {
                    createdOpt += ` ${optDict[key]}`
                }
            }

            // コマンド生成&表示は親コンポーネント
            emit("update-cmd", createdOpt)
        }

        // ########
        // QT制御 #
        // ########
        const testrunNameList = ref([])
        const updateQtTestrun = (nameList) => {
            testrunNameList.value = nameList
        }

        // ####################
        // サイレントモード監視 #
        // * reactiveはcomputedで監視できない
        // ####################
        const isSilent = ref(true)
        watch(optDict, () => {
            isSilent.value = optDict?.silent !== ""
        })

        // ###############
        // ライフサイクル #
        // ###############
        onMounted(async () => {
            await updateOpt()
        })

        return {
            // オプション情報
            optDict,
            // コマンド生成
            updateOpt,
            // QT制御
            updateQtTestrun,
            testrunNameList,
            // サイレントモード監視
            isSilent,
        }
    }
}
</script>
