<template>
    <!-- アプリ情報 -->
    <fieldset id="winApp">
        <legend>Windowsアプリ情報:</legend>
        <div>
            テスト対象のWindowsアプリの情報を入力してください。
        </div>
        <div class="form-warn">
            ■注意事項<br>
            1. 初めてtestrun_winコマンドを実行する際は、「対象アプリ名」と「対象アプリexeパス」の両方を必ず入力してください。<br>
            2. 2回目のtestrun_winコマンド実行以降で、オプションを省略した場合、設定済みのデータを使用します。<br>
            3. 対象アプリexeパスが相対パスの場合、T-DASHの実行ファイルからの相対パスとなります。<br>
        </div>
        <div class="form-area">
            <label for="appName">・対象アプリ名</label><br>
            <input type="text" class="input-text" v-model="inputedAppName" name="appName" @input="updateOpt"
                placeholder="Windowsアプリ名"><br>
            <label for="appPath">
                ・対象アプリexeパス
                <PathHelp />
            </label><br>
            <input type="text" class="input-text" v-model="inputedAppPath" name="appPath" @input="updateOpt"
                placeholder="/Path/to/WindowsApp.exe"><br>
        </div>
    </fieldset>
</template>

<script>
import { onMounted, ref } from 'vue';

import strUtils from '@/js/strUtils';
import cacheUtils from '@/js/cacheUtils';

export default {
    name: 'WinAppForm',
    setup(_, { emit }) {
        // ###############
        // フォームデータ #
        // ###############
        const inputedAppName = ref("")
        const inputedAppPath = ref("")

        // #############
        // コマンド生成 #
        // #############
        const updateOpt = async () => {
            let createdOpt = ""

            // アプリ情報
            if (inputedAppName.value) {
                createdOpt += ` --win_app_name ${inputedAppName.value}`
            }
            if (inputedAppPath.value) {
                inputedAppPath.value = await strUtils.replaceSlush(inputedAppPath.value)
                createdOpt += ` --win_app_path ${inputedAppPath.value}`
            }

            // cacheに保存
            await cacheUtils.saveData("winAppName", inputedAppName.value)
            await cacheUtils.saveData("winAppPath", inputedAppPath.value)

            // コマンド生成&表示は親コンポーネント
            emit("update-opt", "winApp", createdOpt)
        }

        // ###############
        // ライフサイクル #
        // ###############
        onMounted(async () => {
            // cacheから取り出し
            inputedAppName.value = await cacheUtils.getData("winAppName")
            inputedAppPath.value = await cacheUtils.getData("winAppPath")
            await updateOpt()
        })

        return {
            // フォームデータ
            inputedAppName,
            inputedAppPath,
            // コマンド生成
            updateOpt,
        }
    }
}
</script>
