<template>
    <SyncForm :showUseExistingTestOpt=true @update-opt="updateOpt"></SyncForm>
    <TestrunNameForm @update-opt="updateOpt" @update-qt-testrun="updateQtTestrun"></TestrunNameForm>
    <WinAppForm @update-opt="updateOpt"></WinAppForm>
    <TestrunMemoForm @update-opt="updateOpt"></TestrunMemoForm>
    <ReportDirForm @update-opt="updateOpt"></ReportDirForm>
    <AuthProxyForm @update-opt="updateOpt"></AuthProxyForm>
    <!-- 外部連携 -->
    <QtForm :optDict="optDict" :testrunNameList="testrunNameList" @update-opt="updateOpt"></QtForm>
</template>

<script>
import { onMounted, reactive, ref } from 'vue'

import TestrunNameForm from '@/components/forms/TestrunNameForm.vue'
import WinAppForm from '@/components/forms/WinAppForm.vue'
import SyncForm from '@/components/forms/SyncForm.vue'
import TestrunMemoForm from '@/components/forms/TestrunMemoForm.vue'
import ReportDirForm from '@/components/forms/ReportDirForm.vue'
import AuthProxyForm from '@/components/forms/AuthProxyForm.vue'
import QtForm from '@/components/forms/QtForm.vue'

export default {
    name: 'TestrunWinCmd',
    components: {
        TestrunNameForm,
        WinAppForm,
        SyncForm,
        TestrunMemoForm,
        ReportDirForm,
        AuthProxyForm,
        QtForm,
    },
    setup(_, { emit }) {
        // #############
        // フォーム更新 #
        // #############
        const optDict = reactive({
            testrunName: "",
            winApp: "",
            sync: "",
            testrunMemo: "",
            reportsDir: "",
            qt: "",
        })

        // 共通化
        const updateOpt = async (key, opt) => {
            optDict[key] = opt
            await updateOpts()
        }

        // #############
        // コマンド生成 #
        // #############
        const updateOpts = async () => {
            let createdOpt = ""
            for (const key in optDict) {
                if (optDict[key]) {
                    createdOpt += ` ${optDict[key]}`
                }
            }

            // コマンド生成&表示は親コンポーネント
            emit("update-cmd", createdOpt)
        }

        // ########
        // QT制御 #
        // ########
        const testrunNameList = ref([])
        const updateQtTestrun = (nameList) => {
            testrunNameList.value = nameList
        }

        // ###############
        // ライフサイクル #
        // ###############
        onMounted(async () => {
            await updateOpt()
        })

        return {
            // オプション情報
            optDict,
            // コマンド生成
            updateOpt,
            // QT制御
            updateQtTestrun,
            testrunNameList,
        }
    }
}
</script>
